<template>
  <div class="slider-wrapper">
    <spinner class="spinner" v-if="isLoading && !hasError" :size="88" :line-size="10" line-fg-color="#ff9718" line-bg-color="#d4d4d4"/>
    <placeholder v-if="hasError" />
    <div v-else class="tns-outer tns-horizontal tns-subpixel tns-item">
      <div v-for="slide in sliderContent.slides" :key="slide.index">
        <slide :content="slide"></slide>
      </div>
    </div>
  </div>
</template> 

<script>
  import { tns } from "tiny-slider/src/tiny-slider.module";
  import Spinner from "vue-simple-spinner";
  import Content from "./slider-content";
  import Slide from "./Slide";
  import Placeholder from "../placeholder/Placeholder";

  let sliderContent;
  let slider = null;

  export default {
    name: "Slider",
    props: {
      content: {
        type: [String, Object],
        default: {slides: []},
        required: true
      }
    },
    data() {
      return {
        hasError: false,
        isLoading: true,
        sliderContent: {},
        sliderOptions: {
          container: ".tns-outer",
          autoplayHoverPause: true,
          autoplayButtonOutput: false,
          mouseDrag: true,
          controlsText: [
            '<svg fill="#000000" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"/><path d="M0 0h24v24H0z" fill="none"/></svg>',
            '<svg fill="#000000" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"/><path d="M0 0h24v24H0z" fill="none"/></svg>'
          ]
        }
      };
    },
    computed: {
      hasContentUrl() {
        if (typeof this.content === 'string') {
          return true;
        } else {
          return false;
        }
      }
    },
    created() {
      if (this.hasContentUrl) {
        sliderContent = new Content(this.content);
      }
    },
    beforeMount() {
      if (this.hasContentUrl) {
        let vm = this;
        sliderContent.formatSliderContent().then(() => {
          vm.sliderContent = sliderContent.content
          vm.sliderOptions = {...vm.sliderOptions, ...sliderContent.content.options}
          vm.isLoading = false;
        }, () => {
          vm.hasError = true;
        });
      } else {
        this.isLoading = false;
        this.sliderOptions = {...this.sliderOptions, ...this.content.options}
        this.sliderContent = this.content
      }
    },
    updated() {
      if (slider === null) {
        slider = tns(this.sliderOptions);
      }
    },
    components: {
      Slide,
      Spinner,
      Placeholder
    }
  };
</script>

<style lang="scss">
  @import "../../assets/scss/mixins.scss";
  @import "../../assets/scss/variables.scss";

  .spinner {
    position: relative;
    top: 50%;
    transform: translateY(-50%)
  }

  .slider-wrapper {
    @include stretch;
  }

  // tns adds 1 px un-overflown.   
  .tns-inner {
      width: calc(100% + 1px);
  }

  .tns-horizontal.tns-subpixel {
    white-space: nowrap;
    > .tns-item {
      display: inline-block;
      vertical-align: top;
      white-space: normal;
    }
  }

  .tns-outer,
  .tns-outer div {
    @include stretch;
  }

  .tns-outer {

    overflow: hidden;

    .tns-controls {
      z-index: 2;
      position: absolute;
      width: 100%;
      right: 0;
      height: 0;

      button {
        background: inherit;
        border: none;
        padding: 0;

        &:focus {
          outline: none;
        }

        &:hover {
          cursor: pointer;
        }
      }
    }

    .tns-nav {
      z-index: 2;
      position: absolute;
      padding: 10px 0;
      height: 0;
      top: 0;
      text-align: center;

      button {
        background: $light-grey;
        border: none;
        width: 14px;
        margin: 1px;
        height: 18px;
        -webkit-transform: skew(-20deg);
        -moz-transform: skew(-20deg);
        -o-transform: skew(-20deg);
        border-radius: 2px;
        &:focus {
          outline: none;
        }
        &:hover {
          cursor: pointer;
        }
      }
      .tns-nav-active {
        background-color: $orange;
      }
    }

    .tns-controls {
      top: 50%;
    }
  }

  .tns-fadeIn {
    opacity: 1;
    filter: alpha(opacity=100);
    z-index: 0;
  }

  .tns-normal, .tns-fadeOut {
    opacity: 0;
    filter: alpha(opacity=0);
    z-index: -1;
  }

  .tns-gallery {
    position: relative;
    left: 0;
    // overflow: hidden;
    > .tns-item {
      position: absolute;
      left: -100%;
      -webkit-transition: opacity 0s;
      -moz-transition: opacity 0s;
      -webkit-transition: transform 0s, opacity 0s;
      -moz-transition: transform 0s, opacity 0s;
      transition: transform 0s, opacity 0s;
    }
  }

  .tns-controls button:last-child {
    float: right;
  }
</style>




